#land {
  width: 100%;
  height: 20%;

  background-image: url(images/land.png);
	background-repeat: repeat-x;
	background-position: 0px 0px;
  background-size: auto 100%;

	animation: animateLand 9999s linear infinite;
	// TODO: find a way to loop background pattern movement without hacking
	// Problem: background position is calculated in relation to the element, not the background-image itself
}

@keyframes animateLand {
  from { background-position: 0px 0px; }
	to { background-position: 9999 * $game-speed * -1vh 0px; }
}